import { createDate } from './create-date';
import { getMonthNumberOfDays } from './get-month-number-of-days';

interface CreateMonthParams {
  date?: Date;
}

export const createMonth = (params?: CreateMonthParams) => {
  const date = params?.date ?? new Date();

  const d = createDate({ date });
  const { month: monthName, year, monthNumber, monthIndex } = d;

  const getDay = (dayNumber: number) => createDate({ date: new Date(year, monthIndex, dayNumber) });

  const createMonthDays = () => {
    const days = [];

    for (let i = 0; i <= getMonthNumberOfDays(monthIndex, year) - 1; i += 1) {
      days[i] = getDay(i + 1);
    }

    return days;
  };

  return {
    getDay,
    monthName,
    monthIndex,
    monthNumber,
    year,
    createMonthDays,
  };
};
